
.task-table-box {
  width: 1000px;
  margin: 0 auto;
  text-align: center;
}
.task-table-box .task-page {
  margin-top: 10px;
  height: 40px;
}
.task-table-box .task-page .page-content {
  float: right;
}
.task-table-box .task-page .page-content .page-info {
  float: left;
  font-size: 1.1em;
  margin-right: 10px;
  line-height: 40px;
}
.task-table-box .task-page .page-content .page-detail {
  float: left;
  height: 40px;
}
.task-table-box .task-page .page-content .page-detail li {
  float: left;
  width: 30px;
  height: 30px;
  line-height: 30px;
  margin: 5px 5px 0 0;
  background: #b6edce;
  cursor: pointer;
}
.task-table {
  width: 1000px;
  text-align: left;
  cursor: default;
}
.task-table .task-btn-box {
  position: absolute;
}
.task-table .task-tr {
  height: 40px;
  line-height: 40px;
  overflow: hidden;
  font-size: 0;
}
.task-table .task-tr:nth-child(1) {
  background: #b6edce;
  font-weight: bold;
  text-align: center;
  border-top: 1px solid #999;
  border-bottom: 1px solid #999;
}
.task-table .task-tr:nth-child(2n+2) {
  background: #f0fbf5;
}
.task-table .task-tr:nth-child(2n+3) {
  background: #f8fdfa;
}
.task-table .task-tr.active {
  background: #bdefd3;
}
.task-table .task-tr .task-td {
  font-size: 14px;
  padding: 0 10px;
  display: inline-block;
  vertical-align: top;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.task-table .task-tr .task-td .task-input {
  width: 100%;
  height: 30px;
}
.task-table .task-tr .task-td:nth-child(1) {
  width: 110px;
}
.task-table .task-tr .task-td:nth-child(2) {
  width: 370px;
}
.task-table .task-tr .task-td:nth-child(3) {
  width: 70px;
}
.task-table .task-tr .task-td:nth-child(4) {
  width: 150px;
}
.task-table .task-tr .task-td:nth-child(5),
.task-table .task-tr .task-td:nth-child(6),
.task-table .task-tr .task-td:nth-child(7) {
  width: 100px;
  text-align: center;
}


