
.task-modal {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background: #808080;
  width: 250px;
  height: 220px;
  overflow: hidden;
}
.task-modal .close {
  position: absolute;
  top: 5px;
  right: 5px;
  cursor: pointer;
  font-size: 20px;
}


